<template>
  <v-container class="fill-height">
    <v-row class="justify-center">
      <v-col cols="6" class="Aligned flex item">
        <v-tabs
          v-model="tab"
          background-color="deep-purple accent-4"
          class="elevation-2"
          dark
          :centered="true"
          :grow="true"
          :icons-and-text="icons"
        >
          <v-tab :key="1" :href="`#tab-1`">
              Pretensões
            </v-tab>
          
          <v-tab :key="2" :href="`#tab-2`">
              custos
          </v-tab>

          <v-tab :key="3" :href="`#tab-3`">
              Hora técnica
          </v-tab>

          <v-tab :key="4" :href="`#tab-4`">
              Projetos
          </v-tab>

            <v-tab-item
                    :key="1"
                    :value="'tab-' + 1"
                  >
                  <v-row align="center"
                    justify="center">
                    <v-col cols="8"> 
                      Informe suas pretensões
                      <br>
                      <br>
                      <v-text-field
                        label="Quanto quer ganhar de salário liquido por mês?"
                        v-model="salarioLiquido"
                        @input="calculo"
                        outlined
                        dense
                        prefix="R$"
                        :min="0"
                        type="number"
                      />
                      <v-text-field
                        label="Quer trabalhar quantas horas por dia ?"
                        v-model="horasDia"
                        @input="calculo"
                        outlined
                        dense
                        type="number"
                        :min="0"
                      />
                      <v-text-field
                        label="Quer trabalhar quantos dias por semana ?"
                        v-model="diasSemana"
                        @input="calculo"
                        outlined
                        dense
                        type="number"
                        :min="0"
                      />      
                      <v-text-field
                        label="Quantas semanas de férias por ano ?"
                        v-model="semanaFerias"
                        @change="verificaFerias"
                        @input="calculo"
                        outlined
                        dense
                        type="number"
                        :max="47"
                        :min="0"
                      />
                      
                    </v-col>

                  </v-row>
                  <v-row>
                    <v-col align="right">
                     <v-btn @click="tab='tab-2'" small color="primary">Proximo</v-btn>
                     </v-col>
                  </v-row>
            </v-tab-item>

            <v-tab-item
                    :key="2"
                    :value="'tab-' + 2"
                  >

                  
                 <v-row align="center"
                    justify="center">
                    <v-col cols="8"> 
                      <v-form>
                      <v-text-field
                          label="nome"
                          v-model="form.dNome"
                          outlined
                          dense
                        ></v-text-field>
                        <v-text-field
                          label="valor"
                          v-model="form.dValor"
                          outlined
                          dense
                          prefix="R$"
                          type="number"
                        ></v-text-field>
                        <v-btn @click="incluir(form)" small color="primary">Salva</v-btn>
                        </v-form>
                        <ul>
                            <li v-for="(despesa, index) in despesas" :key="index">
                            {{ despesa.nome }}
                            {{ despesa.valor }}
                            <v-btn @click="remove(index)" small color="primary">{{despesa.id}}</v-btn></li>
                        </ul>
                      </v-col>
                  </v-row>
                  <v-row>
                     <v-col align="left">
                     <v-btn @click="tab='tab-1'" small color="primary">Anterior</v-btn>
                     </v-col>
                    <v-col align="right">
                     <v-btn @click="tab='tab-3'" small color="primary">Proximo</v-btn>
                     </v-col>
                  </v-row>
            </v-tab-item>

            <v-tab-item
                    :key="3"
                    :value="'tab-' + 3"
                  >
              <v-row align="center"
                    justify="center">
                    <v-col cols="8">
                      <v-text-field
                        label="sua hora tecnica é:"
                        v-model="horaTecnica"
                        outlined
                        dense              
                        type="number"
                        readonly
                      ></v-text-field>
                    
                  </v-col>
                </v-row>
                <v-row>
                  <v-col align="left">
                     <v-btn @click="tab='tab-2'" small color="primary">Anterior</v-btn>
                     </v-col>
                    <v-col align="right">
                     <v-btn @click="tab='tab-4'" small color="primary">Proximo</v-btn>
                   </v-col>
                </v-row>
            </v-tab-item>
            
            <v-tab-item
                    :key="4"
                    :value="'tab-' + 4"
                  >
                jobs

            
            </v-tab-item>

          </v-tabs>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mask } from 'vue-the-mask'
  export default {
    directives: {
      mask,
    },
    name: 'Calculadora',
    data: () => ({
      salarioLiquido: '',
      horasDia: '',
      diasSemana: '',
      semanaFerias: '',
      totalDespesas: '',
      horaTecnica: '',
      horasAno: '',
      lucroAno: '',
      jobDias: '',
      jobHoras: '',
      jobTotal: '',
      min: '',
      form: {        
        dNome: '',
        dValor: ''
      },
      tab: null,
        text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
        icons: false,
        centered: false,
        grow: true,
        tabs: 3,
      despesas: [
        {nome: 'teste', valor: '800'}
      ]
    }),
    created () {
      this.contador()
    },
    methods: {
      calculo () {          
          if(this.semanaFerias){
              this.horasAno = this.horasDia*this.diasSemana*(48-this.semanaFerias)
          }else{
            this.horasAno = this.horasDia*this.diasSemana*(48)
          }
          if(this.totalDespesas){              
          this.lucroAno = (parseFloat(this.salarioLiquido) + parseFloat(this.totalDespesas))*12
          }else{
            this.lucroAno = parseFloat(this.salarioLiquido)*12
          }          
          let calcula = (this.lucroAno/100*110)/this.horasAno
          this.horaTecnica = calcula.toFixed(2)
      },
      contador () {
        this.totalDespesas = 0
        const desp = this.despesas
        desp.forEach((despesa) => {
          console.log(despesa.valor)          
          if(!this.totalDespesas){
            this.totalDespesas = parseFloat(despesa.valor)
          } else {
          this.totalDespesas = parseFloat(this.totalDespesas)+parseFloat(despesa.valor)
          }
          console.log(this.totalDespesas)
        })
      },
      incluir (desp) {
        console.log(desp.dNome + ' ' + desp.dValor)
        this.despesas.push({nome: desp.dNome, valor: desp.dValor})
        this.form.dNome = ''
        this.form.dValor = ''
        this.contador()
        this.calculo()
      },
      remove (despesa) {
        this.despesas.splice(despesa, 1)
        this.contador()
        this.calculo()
      },
      proximo () {
        this.tab = 'tab-2'
      },
      verificaFerias () {
        if(this.semanaFerias > 47){
          this.semanaFerias = 47
        }
        if(this.semanaFerias < 0 ){
          this.semanaFerias = 0
        }
        this.calculo()
      },
      calcjobs: function () {
          if(!this.jobHoras){
              this.jobHoras = 0
          }
          if(!this.jobDias){
              this.jobDias = 0
          }
          this.jobTotal = this.horaTecnica*this.jobHoras*this.jobDias
      }       
    }
}
</script>
